@import "variables";

.bpa-blog {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $grey_section_bg;

    h3 {
        font-size: 48px;
        line-height: 56px;
        font-weight: 300;
        font-stretch: normal;
        margin: 70px 0 24px 0;
        color: #333333;

        @media all and (max-width: 1440px) {
            margin-top: 60px;
        }
    }

    p {
        font-size: 16px;
        font-weight: normal;
        line-height: 1.5;
        color: #666666;
        text-align: center;
        margin-bottom: 45px;
    }
}

.bpa-blog-items {
    display: flex;
    justify-content: center;
}

.bpa-post {
    max-width: 22.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 15px 0 15px;

    a { 
        margin: 24px 0 56px 0;

        font-size: 14px;
        font-weight: 300;
        font-stretch: normal;
        line-height: 1.43;
        text-align: center;
        color: #666666;
        text-decoration: none;

        &:hover {
            color: $brand_color
        }
    }
}

.read-more-block {
    border-bottom: 1px solid $brand_color;
    width: 8.563rem;
    text-align: center;
    margin-bottom: 80px;

    @media all and (max-width: 1440px) {
        margin-bottom: 70px;
    }

    @media all and (max-width: 500px) {
        margin-bottom: 50px;
    }

    a {
        font-size: 18px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: 0.3px;
        text-align: center;
        color: $brand_color;
        text-decoration: none;
        text-transform: uppercase;

        &:hover {
            text-decoration: none;
            color: #de3c21;
        }
    }

    i {
        border: solid $brand_color;
        border-width: 0 3px 3px 0;
        display: inline-block;
        padding: 4px;
    }
    
    .right {
        transform: rotate(-45deg);
        -webkit-transform: rotate(-45deg);
    }
}

@media all and (max-width: 767px) {
    .bpa-blog {
        .bpa-blog-items{
            flex-direction: column;
            align-items: center;
        }
        h3 {
            margin: 40px 0 32px 0 !important;
        }
        p {
            padding: 0 30px 0 30px;
            br {
                display: none;
            }
        }
        a {
            margin: 17px 0 44px 0; 
        }
        .read-more-block {
            margin-bottom: 60px;
        }
        
    }
}

@media all and (max-width: 500px) {
    .bpa-blog {
        .bpa-blog-items{
            flex-direction: column;
            align-items: center;
        }
    }
}


@media all and (max-width: 414px) {
    .post-img__wraper {
        img {
            width: 100%;
            height: auto;
        }
    }    
}

@import "variables";

.page-business-process-automation {
  backface-visibility: hidden;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;

  .owl-bpa-carousel .owl-stage {
    display: flex;
  }

  .button-with-block {
    hr {
      margin-top: 0;
    }
  }
}

.business-process-automation-sub__header {
  display: block;
  font-size: .71em;
  font-weight: 500;
  text-align: right;
  line-height: 1.34;
  letter-spacing: -0.9px;
}

.grey-bg__section {
  background: #f2f2f2;
  overflow: hidden;
}

.page-business-process-automation {
  .section-subheading {
    font-size: 42px;
    max-width: 14em;
    line-height: normal;
    padding: 1.3em 0 1em;
    margin: 0 auto;
  }

  .section-description {
    width: auto;
    max-width: 960px;
    margin: auto;
    padding: 0 30px;
    box-sizing: border-box;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.6;
    text-align: justify;

    &.centered-description {
      text-align: center;
      margin: -9px auto 0;
      @media all and (min-width: 1400px) and (max-width: 1600px) {
        margin: 0 auto -10px;
      }
      @media all and (min-width: 768px) and (max-width: 1024px) {
        font-size: 18px;
        margin: -4px auto 0;
      }
    }
  }
}

.business-process-automation-services-list {
  position: relative;
  z-index: 4;
  display: flex;
  flex-wrap: nowrap;
  align-items: flex-start;
  justify-content: center;
  margin: 44px auto;
  width: auto;
  max-width: 1180px;
  padding: 0 30px;

  li {
    margin: 0 10px;
    width: 20%;
  }
}

.business-process-automation-services-item {
  display: block;
  text-decoration: none;
  transition: opacity 0.2s ease;
}

.business-process-automation-services-item:hover {
  opacity: 0.8;
}

.business-process-automation-services-item .icon-wrapper {
  display: block;
  float: none;
  width: 64px;
  height: 60px;
  margin: auto;
}

.interface-image {
  height: 100%;
  width: 100%;
}

.business-process-automation-services-item .icon-description {
  display: block;
  text-align: center;
  color: $white;
  font-size: 18px;
  font-weight: 300;
  margin-top: 1em;
}

// benefits section
.page-business-process-automation .benefits-grid__wrapper {
  display: flex;
  justify-content: center;
}

.page-business-process-automation .inner {
  display: flex;
  flex-direction: column;
  align-items: center;

  h3 {
    font-size: 48px;
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
