.bpa-technologies {
    .owl-bpa-carousel {
        padding-bottom: 70px;
        margin-bottom: 70px;

        @media all and (max-width: 680px) {
            padding-bottom: 45px;
            margin-bottom: 45px;
        }

        i {
            border: solid #adadad;
            border-width: 0 4px 4px 0;
            border-radius: 3px;
            display: inline-block;
            padding: 7px;
            top: 40px;
          }

        button {
        width: 32px;
        }

        .right {
        right: calc(50% - 15%);
        transform: rotate(-45deg);
        -webkit-transform: rotate(-45deg);
        }
        
        .left {
        left: calc(50% - 15%);
        transform: rotate(135deg);
        -webkit-transform: rotate(135deg);
        }
    }

    .owl-bpa-carousel .item {
        display: flex;
        align-items: center;
        flex-direction: column;

        h5 {
            font-size: 32px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
