    .owl-item {
        min-height: 20.625rem;
    }
    .owl-dots .owl-dot {
        display: inline-block;
        width: 7px;
        margin: 0 7px;
        opacity: .5;
        transition: opacity 100ms ease;
        box-sizing: content-box;
        overflow: hidden;
        height: 7px;
        cursor: pointer;
        background-color: #d8d8d8 !important;
        border: none !important;
        border-radius: 3rem;
    }
    .owl-dots {
        bottom: 0;
    }
    .owl-dots .owl-dot.active {
        background-color: #666666 !important;
    }
    .owl-nav {
        color: #adadad;
        font-size: 44px;

        position: absolute;
        top: -21px;
        margin-left: auto;
        margin-right: auto;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        button.owl-prev {
            margin-right: 13%;
        }

        button.owl-next {
            margin-left: 13%;
        }
